<template>
	<div class="summary">
		<div class="container">
			<h2 class="header">{{ dataProps.title }}</h2>
			<div class="summary_container">
				<tab-menu :content="dataProps.content"
				          @tab-click="handlerClick($event)"
				></tab-menu>
				<tab-contacts :content="courentText"></tab-contacts>
			</div>
		</div>
	</div>
</template>

<script>
	let tabMenu = require('./TabMenu/TabMenu.vue').default;
	let tabContacts = require('./TabDescriptions/Contacts.vue').default;

	export default {
		name: "rezume",
		props: ['dataProps'],
		data() {
			return {
				courentText: ''
			}
		},
		mounted() {
			this.$anime({
				targets: this.$el,
				left: 0,
				duration: 500,
				easing: 'easeInOutSine'
			});
		},
		created() {
			this.courentText = this.dataProps.content.contacts;
		},
		components: {
			tabMenu,
			tabContacts
		},
		methods: {
			handlerClick(e) {
				for(let key in this.dataProps.content) {
					this.dataProps.content[key].show = false;
				}
				this.courentText = this.dataProps.content[e];
				this.dataProps.content[e].show = true;
			}
		}
	}
</script>

