window.Vue = require('vue/dist/vue.esm').default;
import VueAnime from 'vue-animejs';
//
Vue.use(VueAnime);

require('./components/components.js');

let api;
fetch('./api/main.json')
	.then((res) => {
		return res.json();
	})
	.then((request) => {
		api = request;
		// console.log(typeof api);
})
	.then(() => {
	window.app = new Vue({
		el: '#app',
		props: ['hello'],
		data() {
			return {
				resData: api,
				curentPage: 'my-main',
				pageProps: ''
			}
		},
		methods: {
			menuBtnClick(name) {
				this.curentPage = `my-${name}`;
				this.pageProps = this.resData.blocks[name];
			},
		},
		created() {
			this.pageProps = this.resData.blocks.main;
		},
		//beforeUpdate() {
		//	console.log(this.curentPage);
		//}
	})
})