<template>
	<div :class="{menuMobile: mobileMenu}">
		<div v-for="(element, index) in menu"
		     :class="`${index}-btn menu-element`"
		     @click="$emit('btn-click', index)"
		     v-if="index !== curentPage.slice(3)"

		>

			<div v-html="element.svg"></div>
			{{ index }}
		</div>
		<div v-if="mobileMenu" class="toggle-menu" @click="menuToggle">
			<span v-if="menuOpen">
				<svg class="inline-svg-icon" width="24px" height="24px">
					<use xlink:href="#svg-clothe"></use>
				</svg>
			</span>
			<span v-else>
				<svg class="inline-svg-icon" width="24px" height="24px">
					<use xlink:href="#svg-menu"></use>
				</svg>
			</span>
		</div>
	</div>
</template>
<style lang="scss">
	@import "../../../styles/grid/wanted";
	.menu-element{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		transition: 0.3s color linear;
		z-index: 1001;
		min-width: 71px;
		position: relative;
		@include m_{
			position: absolute;
		}
		svg{
			width: 20px;
			height: 20px;
			transition: 0.3s fill linear;
			@include m_{
				width: 40px;
				height: 40px;
			}
		}
		&.summary-btn{
			@include m_{
				top: calc(50% - 62px);
				left: 20px;
			}
			color: #276380;
			svg{
				fill: #276380;
			}
		}
		&.main-btn{
			@include m_{
				top: 20px;
				left: 20px;
			}
			color: #909090;
			svg{
				fill: #909090;
			}
		}
		&.works-btn{
			@include m_{
				bottom: 20px;
				left: 20px;
			}
			color: #344d98;
			svg{
				fill: #344d98;
			}
		}
		&.contact-btn{
			@include m_{
				bottom: 20px;
				left: calc(50% - 35px);
			}
			color: #7D859D;
			svg{
				fill: #7D859D;
			}
		}
		&:hover{
			color: white;
			svg{
				fill: white;
			}
		}
	}
	.menuMobile{
		position: fixed;
		display: flex;
		align-content: center;
		justify-content: center;
		flex-wrap: wrap;
		top: 0px;
		z-index: 1001;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 2, 34, 0.9);
		left: -100vw;
		padding: 20px;
		overflow-y: scroll;
		.menu-element{
			margin-bottom: 20px;
		}
		.toggle-menu{
			position: fixed;
			top: 10px;
			left: 10px;
			z-index: 1002;
			svg{
				fill: #909090;
			}
		}
	}
</style>
<script>
	export default {
		name: 'my-menu',
		props: ['menu', 'curentPage'],
		data() {
			return {
				mobileMenu: true,
				menuOpen: false
			}
		},
		methods: {
			menuToggle() {
				if(!this.menuOpen) {
					this.$anime({
						targets: this.$el,
						left: 0,
						duration: 500,
						easing: 'easeInOutSine'
					});
					this.menuOpen = true;
				} else {
					this.$anime({
						targets: this.$el,
						left: '-100vw',
						duration: 500,
						easing: 'easeInOutSine'
					});
					this.menuOpen = false;
				}
			},
			checkMobile() {
				if(window.innerWidth < 768) {
					this.mobileMenu = true;
					return;
				}
				this.mobileMenu = false;
			}
		},
		created() {
			this.checkMobile();
		},
		mounted() {
			window.addEventListener('resize', () => {
				if(window.innerWidth < 768) {
					this.mobileMenu = true;
					return;
				}
				this.mobileMenu = false;
			});
			//console.log(this.pageProps);
		}
	}
</script>